                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="form-group">                                
                                <label>{{$t('AssignTo')}}:</label><span class="text-danger">*</span><br/>
                                <Field :rules="{ 'required': true }" name="AssignUserType" v-slot="{ errors }">
                                    <input type="radio" asp-for="AssignUserType" value="User" name="AssignUserType" v-model="txtAssignUserType" id="User" checked="checked" @change="AssignUserType"/> User  
                                    <input type="radio" asp-for="AssignUserType" value="Client" name="AssignUserType" v-model="txtAssignUserType" id="Client" @change="AssignUserType" /> Client 
                                    <span v-if="errors[0]" name="AssignUserType" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>                          
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="form-group" v-if="!disabled">
                                <label>{{$t('SelectUser')}}:</label><span class="text-danger">*</span>
                                <Field :rules="{ 'required': true }" class="w-100" name="User" v-slot="{ errors }">      
                                    <select name="Users" v-model="Users" v-bind:class="{ 'form-control w-100': true ,'is-invalid': errors[0]}">
                                        <option value="">{{$t('Select')}}</option>
                                        <option v-for="(item, itemIndex) in UsersData" :key="itemIndex" :value="item.USER_ID">{{ item.USERNAME }}</option>
                                    </select>
                                    <span v-if="errors[0]" name="Users" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                            </div>
                            <div class="form-group"  v-if="disabled">
                                <label>{{$t('ExistingClient')}}:</label><span class="text-danger">*</span>
                                <Field :rules="{ 'required': true }" class="w-100" name="Client" v-slot="{ errors }">      
                                    <select name="Client" v-model="Users" v-bind:class="{ 'form-control w-100': true ,'is-invalid': errors[0]}">
                                        <option value="">{{$t('Select')}}</option>
                                        <option v-for="(item, itemIndex) in ClientData" :key="itemIndex" :value="item.user_id">{{ item.USERNAME }}</option>
                                    </select>
                                    <span v-if="errors[0]" name="Client" class="invalid-feedback d-block">{{ errors[0] }}</span> 
                                </Field>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="form-group">
                                <label>{{$t('NotificationUser')}}:</label>
                                <Field :rules="{ 'required': false }" class="w-100" name="NotificationUser">      
                                    <select name="NotificationUser" v-model="Users1" v-bind:class="{ 'form-control w-100': true }">
                                        <option value="">{{$t('Select')}}</option>
                                        <option v-for="(item, itemIndex) in NotificationUsersData" :key="itemIndex" :value="item.USER_ID">{{ item.NAME }}</option>
                                    </select>                                  
                                </Field>
                            </div>
                        </div>                        
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="input-group">
                                <label>{{$t('DateofAssignment')}}:</label><span class="text-danger">*</span>
                                <v-date-picker :popover="popover" :min-date="getMinDate()" v-bind:name="DateofAssignment"
                                    :minute-increment="5" class="timedate  input-group" v-model="DateofAssignment">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="MM/DD/YYYY" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                    </template>
                                </v-date-picker>                                
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                            <div class="input-group">
                                <label>{{$t('DateofAssignmentTill')}} </label><span class="text-danger">*</span>
                                <v-date-picker :popover="popover" :min-date="getMinDate()" v-bind:name="DateofAssignmentTill"
                                    :minute-increment="5" class="timedate  input-group" v-model="DateofAssignmentTill">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input placeholder="MM/DD/YYYY" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                    </template>
                                </v-date-picker>                                
                            </div>
                        </div>
                        <div class="col-lg-12">
                            <div class="form-group"> 
                                <label for="GroupDetail">{{$t('Remarks')}}:</label>                           
                                <Field class="w-100" name="GroupDetail" :rules="{ 'required': false }">
                                     <textarea cols="5"  rows="5" v-model="Remarks" v-bind:class="{ 'form-control w-100': true }" />                               
                                </Field>
                            </div>
                        </div>
                    </div> -->
                <Form ref="ProductAssignmentForm">
                    <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons"  :schema="FormSchema"></dynamic-form> 
                    <SearchAsset v-if="DataLoaded"  :GetParentDetails="GetParentDetails" @GetDetailsofParent="GetDetails" @AssestArrayData="AssestArrayData"></SearchAsset>
                    <div class="row flex-row-reverse py-3">
                        <div class="col-lg-6 text-right">                            
                            <a  href="javascript:;"  v-on:click="SubmitData"
                                class="btn btn-success formbtn text-white mr-1" :title="$t('Save')"><i class="fa fa-save pr-2"></i>
                                <span>{{$t('Save')}}</span></a>
                            <a href="javascript:;" @click="onCancel()"  :title="$t('Cancel')" class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{$t('Cancel')}}</span></a>
                        </div>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <small class="text-danger">{{$t('MandatoryString')}}</small>
                            </div>
                        </div>
                    </div>
                </Form> 
            </div> 
        </div>
    </section>
</template>
<script>
import DataService from '../../service/DataService';
import SearchAsset from '../../components/Modal/SearchAsset.vue'

export default{
     components:{SearchAsset},
    data(){
        return{  
            DataLoaded:0,        
            isLoading:false,               
            UsersData:[],         
            DateofAssignment:new Date(),
            Users:'',          
            Remarks:'',
            DateofAssignmentTill:new Date(),
            Groupname:'',
            Users1:'',
            Users2:'',      
            popover: {
                visibility: 'click'
            },
            UserId:null,
            GetParentDetails: null,
            SelfCheckout:null,
            RequestFrom :'',
            isUsedFor :'',
            selectedOption:'',
            checked:true,
            unchecked:false,
            txtAssignUserType:'User',
            disabled:false,           
            dataArray:[],
            buttons:[],
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Departments'),
                            name: "Departments",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true}, 
                            config: {  
                                options: []
                            }                           
                        },
                        {
                          astype: "RadioField",
                          label: this.$t('AssignTo'),
                          name: "AssignTo",
                          value: "User",
                          placeholder: "",
                          config:{
                            options: [{value: "User", name: "User"},{value: "Client", name: "Client"}],                          
                            onChange: this.AssignUserType                       
                          },                        
                          validationRules: {"required": true}                          
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SelectUser'),
                            name: "User",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: { "required": true},
                            config: {  
                                options: []
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ExistingClient'),
                            name: "Client",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            validationRules: { "required": true},
                            config: {  
                                options: []
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('NotificationUser'),
                            name: "NotificationUser",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true},
                            config: {                               
                                options: []
                            },
                        },
                        {
                          astype: "DateField",
                          label: this.$t('DateofAssignment'),
                          name: "DateofAssignment",
                          value: (new Date()),
                          placeholder: "",
                          validationRules: "required",
                          config:{
                            format: "MM/DD/YYYY",
                            minDate: (new Date())                           
                          }
                        },
                        {
                          astype: "DateField",
                          label: this.$t('DateofAssignmentTill'),
                          name: "DateofAssignmentTill",
                          value: (new Date()),
                          placeholder: "",
                          validationRules: "required",
                          config:{
                            format: "MM/DD/YYYY",
                            minDate: (new Date())                           
                          }
                        }                   
                    ]
                },
                {
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Remarks'),
                            name: "Remarks",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            config: {
                                rows:5
                            }
                        }
                    ]
                }       
            ],
            buttons: [
                {
                    isVisible:false,
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],           
        }
    },
    created: async function(){         
        await this.GetDepartment();
        await this.GetNotificationUser();
        await this.GetCompanyUsers(); 
        await this.GetCompanyClients();
        this.UserId=this.GetUserInfo.ID;
        this.DataLoaded=1;    
    },    
    methods:{       
        AssignUserType:async function(ths){                            
            if(ths.target.attributes.id.value =='AssignTo_1'){                         
                (this.FormSchema)[0].Data[2].visibility=false;
                (this.FormSchema)[0].Data[3].visibility=true; 
            }else{                   
                (this.FormSchema)[0].Data[2].visibility=true;
                (this.FormSchema)[0].Data[3].visibility=false;                          
            }
        },
        BackTolist:function(){
            this.$router.push({name:'ProductAssignment'});
        },
        onCancel:function(){
            this.$router.push({name:'ProductAssignment'});
        },
        GetDepartment:async function(){
            let url=`includeInactiveStatus=true&isTicketHandler=false`;
            await DataService.GetDepartment(url).then(response=>{
                response.data.forEach((item) => {
                    (this.FormSchema)[0].Data[0].config.options.push({
                        name:`${item.department_name}`,
                        value:`${item.department_id}` 
                    })
                }) 
            });
        },
        GetNotificationUser:async function(){            
            let url=`moduleName=ASSETASSIGNMENT&department_ids=`;
            await DataService.GetNotficationUsers(url).then(response=>{  
                response.data.data.forEach((item) => {
                    (this.FormSchema)[0].Data[4].config.options.push({
                        name:`${item.NAME}`,
                        value:`${item.USER_ID}` 
                    })
                })
            });
        },
        GetCompanyUsers:async function(){
            let url=`moduleName=ASSETASSIGNMENT`;
            await DataService.GetCompanyUsers(url).then(response=>{
                this.UsersData=response.data;
                response.data.forEach((item) => {
                    (this.FormSchema)[0].Data[2].config.options.push({
                        name:`${item.USERNAME}`,
                        value:`${item.USER_ID}` 
                    })
                })
            });
        }, 
        GetCompanyClients:async function(){            
            let url=`moduleName=ASSETASSIGNMENT&department_ids=`;
            await DataService.GetCompanyClients(url).then(response=>{
                response.data.forEach((item) => {
                    (this.FormSchema)[0].Data[3].config.options.push({
                        name:`${item.USERNAME}`,
                        value:`${item.user_id}` 
                    })
                })
            });
        },
        GetDetails: function(){
            var vm = this;            
            vm.GetParentDetails = {
                DepartmentID: vm.txtDepartment,
                UserID: vm.Users,
                GetNotificationUserID: vm.Users1,
                FromDate: vm.DateofAssignment,
                TillDate: vm.DateofAssignmentTill,
                SelfCheckout:null,
                RequestFrom :'assetassignment',
                isUsedFor :'search',                
            }
            sessionStorage.setItem("GetParentDetails",JSON.stringify(vm.GetParentDetails));
        },        
        AssestArrayData(AssestArrayData){                        
            this.dataArray=AssestArrayData; 
        },
        SubmitData:function(){
            var vm=this;          
            vm.$refs.ProductAssignmentForm.validate().then(result=>{               
            if(result){      
                var formData = new FormData();
                var files = [];
                let uniquenames = "";
                var issueToName="";
                for (let i = 0; i < vm.dataArray.length; i++) {
                    if (vm.dataArray.length == 1) {
                        uniquenames = vm.dataArray[0].AssetUniqueName;
                    }
                    else { 
                         uniquenames = uniquenames + "," + vm.dataArray[i].AssetUniqueName;
                        }
                }  
                vm.UsersData.forEach(item=>{                    
                    if(item.USER_ID==parseInt((vm.FormSchema)[0].Data[2].value)){
                        issueToName=item.USERNAME;
                    }
                }) 
                var mainObj = {
                        UserId: (vm.FormSchema)[0].Data[2].value,
                        DepartmentId: (vm.FormSchema)[0].Data[0].value,
                        AssignedFrom: null,
                        AssignedTill: null,
                        AssignedFromDateTime: vm.$options.filters.formatDateTime((vm.FormSchema)[0].Data[5].value),
                        AssignedTillDateTime: vm.$options.filters.formatDateTime((vm.FormSchema)[0].Data[6].value),
                        StartTime: null,
                        EndTime: null,
                        Comment: (vm.FormSchema)[1].Data[0].value,
                        NotificationUsers: (vm.FormSchema)[0].Data[4].value,
                        RequestFrom: 'AssetAssign',
                        IsRequestFromRequisition: false,
                        IssueToName: issueToName,
                        RFID:0,
                        GroupRequistionAssestInfostring:vm.dataArray,
                        itemNames:uniquenames
                };                                       
                if(files.length>0){
                    formData.append("postString", JSON.stringify(mainObj));
                }else{
                    formData.append("postString", JSON.stringify(mainObj));
                }                
                if(vm.dataArray.length>0){                    
                    DataService.SaveGroupAssign(formData).then(response=>{                    
                    if(response.data.status==0){                        
                        if(response.data.existIds==''){
                            vm.ShowAlert(vm.$t("Item assigned successfully"), "success", vm.$t('Alert'));
                            if(vm.$route.query.reqForm=='MyItems'){
                                
                            }else if(vm.$route.query.reqForm=='pendingRequest'){

                            }else{
                                vm.onCancel();
                            }
                        }
                    }else{
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", vm.$t('Alert'));
                    }
                });
                }else{
                    vm.ShowAlert(vm.$t("AddAtLeastOne"), "failure", vm.$t('Alert'));
                }                 
            }
           }) 
        }
    }
}
</script>